import React from 'react';
import {Box, Typography} from "@mui/material";
import {useHistory} from "react-router-dom";
import {makeStyles} from "@mui/styles";

const tasks: ReadonlyArray<{ name: string, path: string }> = [
  {name: ' 1.    ', path: '/tasks1',},
  {name: ' 2.  ', path: '/tasks2',},
  {name: ' 3.     ', path: '/tasks3',},
  {name: ' 4.      ', path: '/tasks4',},
  {name: ' 5.     ', path: '/tasks5',},
  {name: ' 6.     ', path: '/tasks6',},
]

const MainPage = () => {
  const styles = useStyles();
  const history = useHistory();

  return (
    <Box display={'flex'} justifyContent={"space-around"} pt={10}>
      <Box display={'flex'} flexDirection={'column'} width={600}>
        <Typography variant={'h3'} fontWeight={'700'} px={1.5}>:</Typography>
        <Box display={'flex'} flexDirection={'column'} mt={3}>
          {tasks.map(({path, name}) => (
            <Box px={1.5} py={1.5} className={styles.buttonContainer} onClick={() => history.push(path)}>
              <Typography variant={'h6'}>
                {name}
              </Typography>
            </Box>
          ))}
        </Box>
      </Box>
    </Box>
  );
};

const useStyles = makeStyles((theme) => {
  return {
    buttonContainer: {
      cursor: 'pointer',
      borderRadius: 4,
      '&:hover': {
        backgroundColor: 'rgba(0,0,0,.12)',
      }
    }
  }
})

export default MainPage;
